import React from "react";

const GenericField = ({type, id, label, placeholder, onChange, name, validationMsg})=>{
    return(
        <div className="form-group">
            <label>{label}</label>
            <input type={type} className="form-control is-invalid" id={id} placeholder={placeholder} onChange={onChange} name={name}/>
            <div className="col-sm-3">
                <div id={`${id}validation`} className="text-danger">
                    {validationMsg}
                </div>
            </div>
        </div>
    );
}

export default GenericField;